<template>
  <div class="home-view">
    <Header></Header>
    <main id="main" tabindex="0" aria-label="Justin Rastelli's portfolio">
      <Projects v-parallax.absY="-0.2"></Projects>
      <Timelapses></Timelapses>
      <Blogs></Blogs>
      <Connect></Connect>
    </main>
  </div>
</template>

<script>
import Header from './header/Header'
import Projects from './projects/Projects'
import Timelapses from './timelapses/Timelapses'
import Blogs from './blogs/Blogs'
import Connect from './connect/Connect'

export default {
  name: 'Home',
  components: {
    Header,
    Projects,
    Timelapses,
    Blogs,
    Connect
  }
}
</script>

<style lang="scss">
@import '../variables.scss';
.section-header {
  font-family: 'Rokkitt', Helvetica, Arial, sans-serif;
  display: inline-block;
  margin: 0;
  font-size: 6em;
  font-weight: 500;
  color: $palette-light-green;
  text-decoration: underline;
  overflow: hidden;
  vertical-align: bottom;
  line-height: 0.8em;
  max-width: 100%;
  &::after {
    content: ".............................................................................................";
    color: transparent;
  }
  &::before {
    content: "...";
    color: transparent;
  }
}
@media (max-width: $mobile-width) {
  .section-header {
    font-size: 3em;
    text-align: center;
    margin-top: 2rem;
    &::after, &::before {
      content: ".................................";
    }
    &::before {
      margin-left: -100%;
    }
    &::after {
      margin-right: -100%;
    }
  }
}
</style>


