<template>
  <transition name="list">
    <div class="list" v-if="showList === true" >
      <div class="post" v-for="(post, index) in posts" :key="index" :id="post.id">
        <button v-on:click="flyTo(index, $event)" :aria-labelledby="post.id + '_lbl'" :aria-expanded="post.expanded">
          <map-marker :marker="post" :expanded="post.expanded" :small="true"></map-marker>
        </button>
        <label class="label" :id="post.id + '_lbl'" :aria-label="post.location.name + ' timelapse'">{{post.location.name}}</label>
      </div>
    </div>
  </transition>
</template>

<script>
import MapMarker from './MapMarker'
export default {
  name: 'list',
  props: ['posts', 'showList'],
  components: {
    MapMarker
  },
  methods: {
    flyTo: function (index, event) {
      let target = {
        'index': index,
        'cords': [this.posts[index].location.longitude, this.posts[index].location.latitude]
      }
      this.$emit('flyTo', {'target': target, 'event': event})
    }
  },
  data() {
    return {
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';

.list {
  position:absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 4.5em;
  z-index: 102;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  overflow: hidden;
  background-color: rgba(26, 78, 67, 0);
  transition: background-color 0.2s ease-out, height 0.2s ease-out;
  button {
    background: none;
    border: none;
    margin: 0;
    cursor: pointer;
  }
  &::-webkit-scrollbar {
    height: 0.65em;
  }
  
  &::-webkit-scrollbar-track {
    box-shadow: none;
  }
  
  &::-webkit-scrollbar-thumb {
    outline: 1px solid $font-gray;
    background-color: rgba(26, 78, 67, 0.9);
    &:hover {
      background-color: $palette-dark-blue;
    }
  }
  &:hover, &:focus-within {
    overflow-x: scroll;
    background-color: rgba(26, 78, 67, 0.75);
    height: 7.5em;
  }
  .post {
    position: relative;
    height: 4.5em;
    &:hover, &:focus-within {
      .label {
        opacity: 1;
      }
    }
  }
  .label {
    white-space: nowrap;
    opacity: 0;
    color: $palette-light-green;
    position: absolute;
    bottom: -1.5em;
    left: 0.7em;
    padding-right: 0.5em;
    transition: opacity 0.45s ease-in-out,
  }
  .marker {
    position: relative;
  }
}

.list-enter-active, .list-leave-active {
  transition: top 0.35s ease-out;
}
.list-enter, .list-leave-to /* .fade-leave-active below version 2.1.8 */ {
  top: -6em;
}
</style>


