<template>
    <div class="twitter">
        <div class="header"><h3>Tweets</h3> by <a href="https://twitter.com/Gregle_" title="@Gregle_ on twitter">@Gregle_</a></div>
        <div class="twitter-feed">
            <a class="twitter-timeline" data-dnt="true" data-theme="dark" data-link-color="#91aa9d" href="https://twitter.com/Gregle_?ref_src=twsrc%5Etfw" data-chrome="transparent nofooter noheader" data-tweet-limit="10"></a> 
        </div>
    </div>
</template>

<script>
export default {
  name: "twitter",
  data() {
    return {};
  }
};
</script>

<style scoped lang="scss">
@import "../../variables.scss";
.twitter {
  flex: 1;
  flex-basis: 11em;
  display: flex;
  flex-direction: column;
  color: #f5f8fa;
  position: relative;
  .header {
    padding: 10px;
    border-bottom: 1px solid rgba(145, 165, 180, 0.6);
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    z-index: 10;
    a {
      color: #1b95e0;
      text-decoration: none;
    }
    h3 {
      margin: 0;
      display: inline-block;
      font-weight: 300;
      line-height: 24px;
      font-size: 21px;
    }
  }
  .twitter-feed {
    overflow-y: scroll;
    margin-top: -1px;
    position: absolute;
    top: 45px;
    bottom: 0;
    left: 0;
    right: 0;
    &::-webkit-scrollbar {
      width: 0.5em;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $font-white;
      outline: 1px solid $font-gray;
      height: 300px;
      cursor: pointer;
      &:hover {
        background-color: $palette-light-green;
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .twitter {
    flex-basis: 50em;
  }
}
</style>



