<template>
  <mgl-map
    :accessToken           = "accessToken"
    :mapStyle.sync         = "mapStyle"
    :center.sync           = "center"
    :zoom.sync             = "zoom"
    :scrollZoom            = "scrollZoom"
    class="map"
    ref="map"
    v-on:click="collapseMarkers(-1)" >
    <mgl-navigation-control position="bottom-right"/>
    <post-list :posts="markers" :showList="selectedMarker === undefined" v-on:flyTo="toggleMarker"></post-list>
    <drawer ref="drawer" :post="selectedMarker" v-on:collapse="collapseMarkers" tabindex="0"></drawer>
    <mgl-marker
      v-for="(marker, index) in markers"
      :key="index"
      v-if="marker.location !== null"
      :coordinates="[marker.location.longitude, marker.location.latitude]"
      class="marker"
      anchor="center">
      <map-marker slot="marker" :marker="marker" v-on:expand="toggleMarker" :expanded="marker.expanded"></map-marker>
    </mgl-marker>
  </mgl-map>
</template>

<script>
import { MglMap, MglMarker, MglPopup,
  MglNavigationControl,
  MglGeolocateControl } from 'vue-mapbox'
import Drawer from './Drawer'
import MapMarker from './MapMarker'
import PostList from './PostList'
export default {
  name: 'timelapseMap',
  props: ['posts'],
  components: {
    MglMap,
    MglMarker,
    MglPopup,
    MapMarker,
    MglNavigationControl,
    MglGeolocateControl,
    Drawer,
    PostList
  },
  methods: {
    flyTo: function (cords, zoom) {
      this.$refs.map.flyTo({center: cords, zoom: zoom, around: cords})
    },
    collapseMarkers: function (targetIndex) {
      for (let i = 0; i < this.markers.length; i++) {
        if(i !== targetIndex) {
          this.markers[i].expanded = false
        }
      }
      if (targetIndex === -1) this.selectedMarker = undefined
      this.$forceUpdate()
    },
    toggleMarker: function(event) {
      event.event.stopPropagation()
      this.collapseMarkers(event.target.index)
      this.markers[event.target.index].expanded = true
      this.selectedMarker = this.markers[event.target.index]
      if (event.target.cords && this.$refs.map.center[0] !== event.target.cords[0] &&  this.$refs.map.center[1] !== event.target.cords[1]) {
        this.flyTo(event.target.cords, 12)
      }
    },
    loadData: function (posts) {
      let temp = posts.slice(0)
      for(var i = 0; i < temp.length; i++) {
        temp[i].expanded = false
        temp[i].index = i
      }
      this.markers = temp.slice(0)
    }
  },
  watch: { 
    posts: function(newVal) {
      this.loadData(newVal)
    }
  },
  data() {
    return {
      accessToken: 'pk.eyJ1IjoiamNyYXN0ZWxsaSIsImEiOiJjajhvOGd6cmgwMGt2MnhvM2JjcXZmaXNyIn0.dz4w2BTJ6f_sRDfVpd7yqw',
      mapStyle: 'mapbox://styles/jcrastelli/cjiqxmek61oht2rnnqlp3akaw',
      container: 'map',
      scrollZoom: false,
      zoom: 6,
      center: [-122, 47.5],
      markers: undefined,
      selectedMarker: undefined
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.map {
  height: 85vh;
  max-height: 100em;
  position: relative;
  overflow: hidden;
  width: 100%;
}
</style>


