<template>
  <div>
    <a href="https://twitter.com/Gregle_" title="twitter" target="_blank" aria-label="twitter @gregle_">
      <icon name="twitter" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://github.com/gregle" title="github" target="_blank" aria-label="github gregle">
      <icon name="github" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.instagram.com/gregle90/" title="instagram" target="_blank" aria-label="instagram gregle90">
      <icon name="instagram" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.linkedin.com/pub/justin-rastelli/68/aa7/b06/" title="linkedin" target="_blank" aria-label="linkedin profile">
      <icon name="linkedin" class="icon" :scale="iconSize"></icon>
    </a>
  </div>
</template>

<script>
export default {
  name: 'social-media-icons',
  data () {
    return {
      iconSize: 2
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
a {
  display: inline-block;
  .icon{
    fill: $palette-dark-blue;
    transition: $standard-transition;
  }
  &:hover, &:focus {
    .icon{
      fill: $palette-mid-blue;
    }
  }
  &~a {
    margin-left: $standard-spacing;
  }
}
</style>



