<template>
  <section class="projects" aria-labelledby="projectsHeading" role="section">
    <h2 id="projectsHeading" class="section-header">Projects</h2>
    <div>
      <div class="grid">
        <div class="hex-wrap" v-bind:key="index" v-for="(project, index) in projectData" >
          <a class="hexagon" :href="project.location" v-bind:style="project.styles" :aria-label="project.name + ': ' + project.description">
            <div class="hexagon-in1"><div>&nbsp;</div></div>
            <div class="hexagon-in2"><div>&nbsp;</div></div>
            <div class="inner">
              <div>
                <h3>{{project.name}}</h3>
                <p>{{project.description}}</p>
              </div>
            </div>
          </a>
        </div>
        <div class="placeholder"></div>
        <div class="placeholder2"></div>
        <div class="placeholder3"></div>
        <div class="placeholder4"></div>
        <div class="placeholder5"></div>
        <div class="placeholder6"></div>
      </div>
    </div>
  </section>
</template>

<script>
import ProjectData from '../../assets/json/projects.json'
export default {
  name: 'projects',
  data () {
    return {
      projectData: ProjectData
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, $hexagon-width/2);
  grid-gap: $standard-spacing;
  justify-items: center;
  margin-top: 4.5em;
}
.placeholder {
  grid-row-start: 2;
  grid-column: 1 / span 1;
}
.placeholder2 {
  grid-row-start: 4;
  grid-column: 1 / span 1;
}
.placeholder3 {
  grid-row-start: 6;
  grid-column: 1 / span 1;
}
.placeholder4 {
  grid-row-start: 8;
  grid-column: 1 / span 1;
}
.placeholder5 {
  grid-row-start: 10;
  grid-column: 1 / span 1;
}
.placeholder6 {
  grid-row-start: 12;
  grid-column: 1 / span 1;
}

.projects {
  background-color: $palette-dark-blue;
  > div {
    width: 90%;
    max-width: 1300px;
    margin: 0 auto;
    transition: $standard-transition;
  }
}
.project-tile {
  position: relative;
  display: inline-block;
  margin: $standard-spacing/2;
  .title{
    margin: $standard-spacing/2;
  }
}
.hex-wrap {
  position: relative;
  margin: $standard-spacing2 0;
  width: 13.25em;
  height: 7.65em;
  vertical-align: middle;
  grid-column: span 2;
  display: inline-block;
  &:hover, &:focus {
    z-index: 100;
  }
}
.hexagon {
  height: 100%;
  background-position: 50% 50%;
  background-size: auto 16em;
  display: block;
  text-decoration: none;
  text-align: center;
  transition: $hexagon-hover-transition;
  &:hover, &:focus {
    background-size: auto $hexagon-background-size * 1.15;
    .hexagon-in1, .hexagon-in2, .inner {
      & div{
        opacity: .9;
      }
    }
  }
}
.hexagon-in1, .hexagon-in2 {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: inherit;
  transform: rotate(-60deg);
  transition: $hexagon-hover-transition;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: $hexagon-background-size;
    width: $hexagon-background-size;
    transform: rotate(60deg) translate(-4.3em, -0.91em);
    background: inherit;
  }
  div {
    height: 100%;
    width: 100%;
    background-color: inherit;
    opacity: 0;
    transition: $hexagon-hover-transition;
  }
}
.hexagon-in2 {
  transform: rotate(60deg);
  &:before {
    transform: rotate(-60deg) translate(2.93em, -3.29em);
  }
}

.inner {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: inherit;
  transition: $hexagon-hover-transition;
  div {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: inherit;
    opacity: 0;
    transition: $hexagon-hover-transition;
    padding: .25em;
    h3, p {
      margin: 0;
    }
    h3 {
      text-decoration: underline;
    }
    p {
      padding: .2em;
    }
  }
}

@media (max-width: 1000px) {
  .hex-wrap {
    margin: 0.5em 0;
    transform: scale(0.75);
    p {
      font-size: 1.2em;
    }
  }
  .grid {
    grid-template-columns: repeat(auto-fill, 4.7em);
    grid-gap: 0.8em;
  }
}

@media (max-width: $mobile-width) {
  .grid {
    grid-template-columns: repeat(auto-fill, 4.1em);
  }
  .hex-wrap {
    margin: 0;
    transform: scale(.7);
    p {
      font-weight: bolder;
    }
  }
  .projects {
    & > div {
      width: 85%;
      margin-bottom: 2em;
    }
  }
}
</style>


