<template>
  <transition name="drawer">
    <section aria-label="instagram video panel" class="drawer" v-if="post !== undefined" tabindex="0" >
      <a :href="post.link" title="gregle90 on instagram" aria-label="gregle90 on instagram" class="instagram-info">
        <img class="instagram-profile" :src="post.user.profile_picture"/>
        <p>
          <b>{{post.user.username}}</b> <icon name="instagram" class="icon" :scale="1"></icon><br/>
          {{post.location.name}}
        </p>
      </a>
      <button class="btn-close" v-on:click="close" title="close panel"><icon name="close" class="icon" :scale="1"></icon></button>
      <div class="video-embed">
        <video
          ref="video"
          loop 
          :poster="post.images.standard_resolution.url"
          :aria-label="post.location.name + ' timelapse'"
        >
          <source :src="post.videos.standard_resolution.url" type="video/mp4">
          <p>Your browser doesn't support this kind of video. Here is a <a :href="post.link">link to the video</a> instead.</p>
        </video>
        <button class="btn" v-on:click="playPause" v-bind:class="{ pause: !videoPaused, play: videoPaused }" :aria-label="videoPaused ? 'Play video' : 'Pause video'" :title="videoPaused ? 'Play video' : 'Pause video'">
          <span class="bar bar-1"></span>
          <span class="bar bar-2"></span>
        </button>
      </div>
      <span class="info-span">{{date}}<span><b>{{post.likes.count}}</b>&nbsp;&nbsp;<icon name="heart" class="insta icon" :scale="1"></icon></span></span>
      <p class="caption">{{post.caption.text}}</p>
    </section >
  </transition>
</template>

<script>
export default {
  name: 'drawer',
  props: ['post'],
  computed: {
    date: function() {
      let date = new Date(parseInt(this.post.created_time) * 1000)
      let monthArr = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let month = monthArr[date.getMonth()]
      let year = date.getYear() + 1900
      return month + ' ' + date.getDate() + ', ' + year
    }
  },
  watch: { 
    post: {
      handler: function (newVal, oldVal) {
        if (newVal === undefined) {
          this.pauseVideo()
        }
        if (oldVal !== undefined && newVal !== undefined) {
          this.pauseVideo()
          this.loadVideo()
        }
      },
      deep: true
    }
  },
  methods: {
    close: function () {
      this.$emit('collapse', -1)
    },
    pauseVideo: function () {
      let video = this.$refs.video
      video.pause()
      this.videoPaused = true
    },
    loadVideo () {
      let video = this.$refs.video
      video.load()
    },
    playPause: function () {
      let video = this.$refs.video
      if (video.paused) {
        video.play()
        this.videoPaused = false
      } else {
        video.pause()
        this.videoPaused = true
      }
      video.focus()
    }
  },
  data () {
    return {
      videoPaused: true
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.drawer {
  position:absolute;
  left: 0;
  top: 0;
  bottom: 0;
  max-width: 35em;
  width: 100%;
  z-index: 102;
  background: $font-white;
  box-shadow: 2px 0 10px $palette-dark-blue;
  overflow-x: hidden;
  padding: 1em 1em 4em 1em;
  &::-webkit-scrollbar {
    width: 0.5em;
  }
  
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px $font-white;
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: $palette-mid-tone;
    outline: 1px solid $font-gray;
    height: 300px;
    &:hover {
      background-color: $palette-dark-green;
    }
  }
  .video-embed{
    position: relative;
    video {
      width: 100%;
    }
  }
  .caption {
    border-top: 1px solid $palette-light-green;
    padding-top: 0.5em;
    margin: 0;
  }
}
.insta.icon {
  color: $insta-red;
  vertical-align: middle;
}
.instagram-info {
  text-decoration: none;
  color: $font-gray;
  padding: 2em 0.5em;
  white-space: nowrap;
  p {
    display: inline-block;
    margin: 0;
    vertical-align: middle;
  }
  .icon {
    vertical-align: middle;
    color: $font-gray;
    transition: $standard-transition;
  }
  &:hover, &:focus {
    .icon {
      color: $insta-red;
    }
  }
}
.drawer-enter-active, .drawer-leave-active {
  transition: max-width 0.35s ease-out;
}
.drawer-enter, .drawer-leave-to /* .fade-leave-active below version 2.1.8 */ {
  max-width: 0;
}
.info-span {
  display: flex;
  justify-content: space-between;
  white-space: nowrap;
  margin: 0.5em 0;
}
.instagram-profile {
  border-radius: 5em;
  width: 3em;
  height: 3em;
  margin: 1em 0.5em 1em 0;
  vertical-align: middle;
}
.btn-close {
  position: absolute;
  right: 0.5em;
  top: 0.5em;
  background: none;
  border: none;
  cursor: pointer;
  color: $palette-mid-tone;
  &:hover {
    color: $palette-mid-blue;
  }
}

.btn {
  position: absolute;
  top: 0;
  bottom: 0.55em;
  right: 0;
  left: 0;
  opacity: 0;
  flex: 1;
  width: 100%;
  border: none;
  cursor: pointer;
  background-color: rgba(25, 52, 65, 0);
  transition: border 0.1s ease-in-out,
              transform 0.1s ease-in-out,
              opacity 0.45s ease-in-out,
              background-color 0.5s ease-out;
  &:hover, &:focus {
    background-color: rgba(25, 52, 65, 0.45);
    opacity: 1;
  }
  &.pause:focus:not(:hover) {
    opacity: 0;
  }
  .bar {
    display: inline-block;
    background-color: $font-white;
    width: 0.5em;
    height: 2.35em;
    border-radius: 1em;
    transform-origin: center;
    transition: transform 0.4s ease-in-out, background 0.1s ease-in-out;
  }
  &.pause {
    .bar-1 {
      transform: translateX(0) translateY(-0.5em) rotate(0deg);
    }
    .bar-2 {
      transform: translateX(0.5em) translateY(-0.5em) rotate(0deg);
    }
  }
  &.play {
    .bar-1 {
      transform: translateX(1em) translateY(-1em) rotate(-55deg);
    }
    .bar-2 {
      transform: translateX(0.2em) translateY(0.15em) rotate(-125deg);
    }
  }
}

@media (max-width: $mobile-width) {
  .drawer {
    padding: 0;
  }
  .info-span,
  .caption {
    padding: 0 1em;
  }
}
</style>



