<template>
  <header class="nav">
    <div class="nav-top">
      <router-link to="/" title="jcrastelli.com"><img class='logo' src="../../assets/graphics/logo.png" alt="jcrastelli-logo"/></router-link>
      <button class="skipToMain" v-on:click="skipToMain">Skip to Main Content</button>
      <social-media-icons class="social" :iconSize='1.5'></social-media-icons>
    </div>
    <div class="nav-mid">
      <video ref="video" src="../../assets/videos/videoheader3.mp4" id="video-container" width="100%" autoplay loop muted v-parallax.absY="0.3"/>
      <div class="intro">
        <h1>Welcome, I'm Justin</h1>
        <p>I'm a developer, photographer,<br/>video editor, and all around tinkerer.</p>
      </div>
      <div class="bottom">
        <p>Thanks for coming around to see what I've been up to</p>
        <div class="play-btn-container">
          <icon name="chevron-down" class="icon" :scale="1.25"></icon>
          <button class="btn" v-on:click="playPause" v-bind:class="{ pause: !videoPaused, play: videoPaused }" :aria-label="videoPaused ? 'Play header video' : 'Pause header video'" :title="videoPaused ? 'Play header video' : 'Pause header video'">
            <span class="bar bar-1"></span>
            <span class="bar bar-2"></span>
          </button>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import SocialMediaIcons from './SocialMediaIcons'

export default {
  name: 'header-bar',
  components: {SocialMediaIcons},
  data () {
    return {
      videoPaused: false
    }
  },
  methods: {
    playPause: function () {
      var video = this.$refs.video
      if (video.paused) {
        video.play()
        this.videoPaused = false
      } else {
        video.pause()
        this.videoPaused = true
      }
    },
    skipToMain: function () {
      var main = document.getElementById('main')
      main.focus()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.nav {
  color: $font-white;
  overflow: hidden;
  h1 {
    margin: 0 0 $standard-spacing-half 0;
    text-shadow: 0 0 10px rgb(0, 0, 0);
  }
  .logo {
    width: 125px;
    vertical-align: bottom;
  }
  &-top {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    padding: $standard-spacing3/2 $standard-spacing2;
    margin: 0 auto;
    transition: $standard-transition;
    .social {
      display: inline-block;
      margin: $standard-spacing-half 0;
    }
  }
  &-mid {
    position: relative;
    .intro {
      position: absolute;
      top: 50%;
      right: $standard-spacing2;
      max-width: 25em;
      margin: 0 auto;
      transform: translate(0, -50%);
      text-align: left;
      text-shadow: 0 0 10px rgb(0, 0, 0);
      transition: $standard-transition;
      h1 {
        font-family: 'Rokkitt', Helvetica, Arial, sans-serif;
        margin: 0; 
        font-weight: lighter;
        color: $palette-light-green;
        font-size: 2.25em;
      }
      p {
        margin: 0;
        line-height: 1.1em;
      }
    }
    video {
      width: 100%;
      object-fit: cover;
      min-height: 200px;
    }
  }
}
.skipToMain {
  position: absolute;
  left: -99999px;
  top: 0;
  margin-left: 0;
  text-align: center;
  list-style: outside none;
  background: none;
  color: $font-gray;
  font-weight: cold;
  border: none;
  &:focus{
    position: relative;
    display: inline-block;
    left: 0;
  }
}
.bottom {
  position: absolute;
  bottom: $standard-spacing;
  right: 0;
  left: 0;
  p {
    text-align: center;
    margin: .25em;
  }
  .play-btn-container {
    display: flex;
    margin: 0 1.75em 0 3.5em;
    .icon {
      margin: 0 auto;
      fill: $palette-light-green;
    }
    .btn {
      float: right;
    }
  }
}
.btn {
  position: relative;
  width: 1.75em;
  height: 1.75em;
  border: none;
  border-radius: 7px;
  background: none;
  padding: 0;
  cursor: pointer;
  transition: border 0.1s ease-in-out;
  &:hover, &:focus {
    border-color: $palette-light-green;
    .bar {
      background-color: $palette-mid-tone;
    }
  }
  .bar {
    display: inline-block;
    position: absolute;
    background-color: $font-white;
    top: 3px;
    left: 1px;
    width: 3px;
    height: 14px;
    border-radius: 10px;
    transform-origin: center;
    transition: transform 0.4s ease-in-out, background 0.1s ease-in-out;
  }
  &.pause {
    .bar-1 {
      transform: translateX(6px) translateY(2px) rotate(0deg);
    }
    .bar-2 {
      transform: translateX(14px) translateY(2px) rotate(0deg);
    }
  }
  &.play {
    .bar-1 {
      transform: translateX(10px) translateY(-1px) rotate(-55deg);
    }
    .bar-2 {
      transform: translateX(10px) translateY(6px) rotate(-125deg);
    }
  }
}
@media (max-width: $mobile-width) {
  .nav-top {
    padding: $standard-spacing;
    .social{
      margin: 0 auto;
    }
    a{
      display: none;
    }
  }
  .nav-mid {
    .intro {
      left: 0;
      right: 0;
      transform: translate(0, -50%);
      text-align: center;
      padding: $standard-spacing;
      font-size: .75em;
      h1 {
        margin: $standard-spacing-half/3;
      }
    }
  }
  .bottom {
    font-size: .75em;
  }
  .play-btn-container {
    // padding: 0 $standard-spacing $standard-spacing 0;
  }
}
</style>



