<template>
  <div class="marker mapboxgl-marker mapboxgl-marker-anchor-center " v-on:click="expand($event)" v-bind:class="{'expanded': expanded, 'small': small}">
    <img :src="marker.images.low_resolution.url" />
  </div>
</template>

<script>
export default {
  name: 'MapMarker',
  props: ['marker', 'expanded', 'small'],
  methods: {
    expand: function (event) {
      let target = {
        'index': this.marker.index,
        'cords': [this.marker.location.longitude, this.marker.location.latitude]
      }
      this.$emit('expand', {'target': target, 'event': event})
    }
  },
  data() {
    return {
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.marker {
  padding: 0;
  margin: 0;
  cursor: pointer;
  img {
    object-fit: cover;
    transition: $standard-transition;
    border: 3px solid $palette-dark-blue;
    border-radius: 250px;
    width: 100%;
    width: 5em;
    height: 5em;
    padding: 0;
    margin: 0.25em;
  }
  &.small {
    img {
      width: 3em;
      height: 3em;
      background: rgba(209, 219, 189, 0);
    }
    &:hover img {
      background: rgba(209, 219, 189, 0.5);
      width: 3em;
      height: 3em;
    }
  }
  &:hover {
    z-index: 102;
    img {
      width: 6em;
      height: 6em;
      padding: 0.25em;
      margin: 0;
      filter: brightness(1.15);
    }
  }
  &:focus, &.expanded {
    outline: none;
    z-index: 101;
    img {
      width: 10em;
      height: 10em;
      border: 10px solid $palette-dark-blue;
      padding: 0.75em;
      margin: 0;
      background-color: rgba(209, 219, 189, 0.75);
    }
  }
}
</style>


