<template>
	<div class="timelapses">
    <section v-parallax.absY="-0.25" aria-label="timelapse map" role="section">
      <h2 class="section-header">Timelapses</h2>
      <TimelapseMap :posts="posts"></TimelapseMap>
    </section>
  </div>
</template>

<script>
import TimelapseMap from './Map'
import instagram from '../../gateways/instagram'
export default {
  name: 'timelapses',
  components: {
    TimelapseMap
  },
  created: function () {
    instagram.getUserProfile().then(function (results) {
      this.posts = results.body.data
    }.bind(this))
  },
  data() {
    return {
      posts: []
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.timelapses {
  background-color: $palette-light-green;
  section {
    background-color: $palette-light-green;
  }
  h2 {
    color: $palette-dark-green;
  }
}
</style>


