<template>
  <div class="connect">
    <section aria-labelledby="connectHeading" role="section" v-parallax.absY="-0.2">
      <h2 id="connectHeading" class="section-header">Connect</h2>
      <div class="social">
        <a href="https://twitter.com/Gregle_" title="Gregle_ on Twitter" target="_blank" rel="noopener" class="link-wrap twitter">
          <icon name="twitter" class="icon" :scale="iconSize"></icon>
          <label>Twitter</label>
        </a>
        <a href="https://github.com/gregle" title="Gregle on Github" target="_blank" rel="noopener" class="link-wrap github">
          <icon name="github" class="icon" :scale="iconSize"></icon>
          <label>Github</label>
        </a>
        <a href="https://www.instagram.com/gregle90/" title="Gregle90 on Instagram" target="_blank" rel="noopener" class="link-wrap instagram">
          <icon name="instagram" class="icon" :scale="iconSize"></icon>
          <label>Instagram</label>
        </a>
      </div>
      <div class="email">
        <a href="mailto:justin@jcrastelli.com" title="email: justin@jcrastelli.com" rel="noopener" class="link-wrap">
          <icon name="envelope" class="icon" :scale="iconSize"></icon>
          <label>Justin@jcrastelli.com</label>
        </a>
        <span class="link-wrap heart">
          <icon name="heart" class="icon" :scale="iconSize"></icon>
        </span>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'connect',
  data () {
    return {
      iconSize: 5
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.connect {
  background-color: $palette-light-green;
  min-height: 100vh;
  section {
    background-color: $palette-light-green;
    .social {
      display: flex;
      justify-content: center;
      align-items: space-between;
      flex-wrap: wrap;
      padding-top: $standard-spacing3;
    }
    .link-wrap {
      display: inline-block;
      text-align: center;
      transition: all .1s linear;
      text-decoration: none;
      padding: 0;
      margin: 1.25em 2.25em;
      border-bottom: 0 solid $palette-mid-tone;
      label {
        display: block;
        color: $font-gray;
        font-size: 1.25em;
        font-weight: bolder;
      }
      .icon {
        fill: $palette-mid-tone;
        margin: 0 auto;
      }
      &:not(.heart):hover, &:not(.heart):focus{
        border-bottom: 3px solid $palette-dark-green;
        padding: .25em 1em;
        margin: 1em 1.25em;
        margin-bottom: calc(1em - 3px);
        .icon { fill : $palette-dark-green; }
        &.twitter {
          .icon { fill: #1DA1F2; }
          border-color: #1DA1F2;
        }
        &.instagram {
          .icon { fill: $insta-red; }
          border-color: $insta-red;
        }
        &.github {
          .icon { fill: #181717; }
          border-color: #181717;
        }
      }
    }
    .email {
      display: flex;
      justify-content: center;
      align-items: space-between;
      margin-left: -3em;
    }
    .heart {
      margin-left:-1.5em;
      .icon {
        fill: $insta-red;
      }
    }
  }
  h2 {
    color: $palette-dark-green;
  }
}
@media (max-width: $mobile-width) {
  .connect section .social {
    padding-top: $standard-spacing;
  }
}
</style>



