import Vue from 'vue'

export default {
  getUserProfile () {
    var url = 'https://api.instagram.com/v1/users/self/media/recent/?access_token=2912883595.e534eb5.a692091c362641a4962628a5f3c1e0b8'
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  }
}



