<template>
  <div class="blogs">
    <section aria-labelledby="blogsHeading" role="section" v-parallax.absY="-0.3">
      <h2 id="blogsHeading" class="section-header">README.md</h2>
      <div class="blog-flex">
        <Timeline></Timeline>
        <BlogList></BlogList>
      </div>
    </section>
  </div>
</template>

<script>
import Timeline from './Twitter'
import BlogList from './BlogList'
export default {
  name: 'blogs',
  components: {
    Timeline,
    BlogList
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.blogs {
  font: normal normal 14px/1.4 Helvetica,Roboto,"Segoe UI",Calibri,sans-serif;
  min-height: 102vh;
}
.blog-flex {
  display: flex;
  background-color: $palette-dark-blue;
  & > div {
    margin-bottom: 1em;
  }
}
@media (max-width: $mobile-width) {
  .blog-flex {
    flex-direction: column;
  }
}
</style>



